<template>
    <div class="settings">
    <main>
        <div class="grid1">
            <hgroup class="settings-group">
                <h3 class="settings-title"><i class="fas fa-cog"></i> Settings</h3>
                <p class="settings-p">{{this.$store.state.address}}</p>
            </hgroup>
            <h3><i class="cancel fas fa-times-circle" v-on:click="closeSettings"></i></h3>
        </div>
        
        <hr/>
         <h4 class="header">Weather Cast</h4>
         
         <router-link to="/forecast/today"><p class="rm"><a class="color" href="">Today</a></p></router-link>
         <router-link to="/forecast/tomorrow"><p class="rm"><a class="color" href="">Tomorrow</a></p></router-link>
         <router-link to="/forecast/hourly"><p class="rm"><a class="color" href="">Hourly</a></p></router-link>
         <router-link to="/forecast/past7"><p class="rm"><a class="color" href="">Past 7 days</a></p></router-link>
         <router-link to="/forecast/next7"><p class="rm"><a class="color" href="">Next 7 days</a></p></router-link>
         <router-link to="/forecast/next30"><p class="rm"><a class="color" href="">Next 30 days</a></p></router-link>
        
        <h4 class="header">Unit</h4>
        <select v-model="unit">
            <option value="" disabled selected>Select</option>
            <option value="us">US (°F, miles)</option>
            <option value="metric">Metric (°C, km)</option>
            <option value="uk">UK (°C, miles)</option>
        </select>
        <button class="save-btn" v-on:click.prevent="save"><i class="fas fa-save"></i> Save</button>
    </main>
    </div>
</template>


<script>
export default {
    name:"Settings",
    data(){
        return{
            unit:"",
            distance:""
        }
    },
    methods:{
        closeSettings(){
            this.$emit("close-settings")
        },
        save(){
            if(this.unit == "metric"){
                this.distance = "km"
            }else{
                this.distance = "miles"
            }
            this.$store.commit('unitUpdate',[this.unit,this.distance])
            this.$emit("update-data")
            //console.log(this.unit)
            //console.log(this.$store.state.unit)
            //console.log(this.$store.state.unitDistance)
        }
    }
}
</script>

<style scoped>
.color{
    color: #41993b;
}
.rm{
    margin-bottom: 0;
}
.grid1{
    display: flex;
    justify-content: space-between;
}
.settings{
    background-color: rgba(0.0, 0.0, 0.0, 0.95);
    width: 40%;
    text-align: left;
    padding: 30px;
    position: fixed;
    right: 0;
    top: 0;
    height: 100%;
    z-index: 2;
}

.settings-p{
    margin-bottom: 0;
    font-size: 13px;
}
@media only screen and (max-width:950px){
    .settings{
        width: 50%;
    }
    @media only screen and (max-width:600px){
        .settings{
            width: 60%;
        }
    }
    @media only screen and (max-width:500px){
        .settings{
            width: 100%;
        }
    }
}

.save-btn{
    background-color: #4FB948;
    border: none;
}

.save-btn:hover{
    background-color: #41993b;
}

.header{
    margin-bottom: 5px;
}

.cancel:hover{
    cursor: pointer;
}
</style>

