const url = "api/weather/"

const getCurrentWeather = async (loc, unit)=>{
    try{
        const fetchAll = await fetch(url+`/current/${loc}/${unit}`)
        const res = await fetchAll.json()
        const response = [res]
        return response.map(weather=>({...weather}))
    }catch(err){console.error(err)}
}

