import { createStore } from 'vuex'

export default createStore({
  state: {
    hubbleSettings:false,
    unit:"metric",
    unitDistance:"km",
    search:"",
    address:""
  },
  getters: {
  },
  mutations: {
    hubbleSettingsOpen(state){
      state.hubbleSettings = true
    },
    hubbleSettingsClose(state){
      state.hubbleSettings = false
    },
    unitUpdate(state,payload){
      state.unit = payload[0]
      state.unitDistance = payload[1]
    },
    search(state, payload){
      state.search = payload
    },
    resolveAddress(state, payload){
      state.address = payload
    }
  },
  actions: {
  },
  modules: {
  }
})
