<template>
    <div class="widget1">
            <div class="grid">
                <div class="left-panel panel">
                <div v-if="time == '00:00:00'" class="date">
                    {{date}}<br/>
                    12:00 AM
                </div>
                <div v-else-if="time == '01:00:00'" class="date">
                    {{date}}<br/>
                    1:00 AM
                </div>
                <div v-else-if="time == '02:00:00'" class="date">
                    {{date}}<br/>
                    2:00 AM
                </div>
                <div v-else-if="time == '03:00:00'" class="date">
                    {{date}}<br/>
                    3:00 AM
                </div>
                <div v-else-if="time == '04:00:00'" class="date">
                    {{date}}<br/>
                    4:00 AM
                </div>
                <div v-else-if="time == '05:00:00'" class="date">
                    {{date}}<br/>
                    5:00 AM
                </div>
                <div v-else-if="time == '06:00:00'" class="date">
                    {{date}}<br/>
                    6:00 AM
                </div>
                <div v-else-if="time == '07:00:00'" class="date">
                    {{date}}<br/>
                    7:00 AM
                </div>
                <div v-else-if="time == '08:00:00'" class="date">
                    {{date}}<br/>
                    8:00 AM
                </div>
                <div v-else-if="time == '09:00:00'" class="date">
                    {{date}}<br/>
                    9:00 AM
                </div>
                <div v-else-if="time == '10:00:00'" class="date">
                    {{date}}<br/>
                    10:00 AM
                </div>
                <div v-else-if="time == '11:00:00'" class="date">
                    {{date}}<br/>
                    11:00 AM
                </div>
                <div v-else-if="time == '12:00:00'" class="date">
                    {{date}}<br/>
                    12:00 PM
                </div>
                <div v-else-if="time == '13:00:00'" class="date">
                    {{date}}<br/>
                    1:00 PM
                </div>
                <div v-else-if="time == '14:00:00'" class="date">
                    {{date}}<br/>
                    2:00 PM
                </div>
                <div v-else-if="time == '15:00:00'" class="date">
                    {{date}}<br/>
                    3:00 PM
                </div>
                <div v-else-if="time == '16:00:00'" class="date">
                    {{date}}<br/>
                    4:00 PM
                </div>
                <div v-else-if="time == '17:00:00'" class="date">
                    {{date}}<br/>
                    5:00 PM
                </div>
                <div v-else-if="time == '18:00:00'" class="date">
                    {{date}}<br/>
                    6:00 PM
                </div>
                <div v-else-if="time == '19:00:00'" class="date">
                    {{date}}<br/>
                    7:00 PM
                </div>
                <div v-else-if="time == '20:00:00'" class="date">
                    {{date}}<br/>
                    8:00 PM
                </div>
                <div v-else-if="time == '21:00:00'" class="date">
                    {{date}}<br/>
                    9:00 PM
                </div>
                <div v-else-if="time == '22:00:00'" class="date">
                    {{date}}<br/>
                    10:00 PM
                </div>
                <div v-else-if="time == '23:00:00'" class="date">
                    {{date}}<br/>
                    11:00 PM
                </div>
                <div class="city">
                    {{address}}
                </div>
                <div class="temp">
                   <img v-if="icon == 'clear-day'" src="@/assets/weather-icons/line/clear-day.svg" alt="" width="60">
                   <img v-else-if="icon == 'clear-night'" src="@/assets/weather-icons/line/clear-night.svg" alt="" width="60">
                   <img v-else-if="icon == 'cloudy'" src="@/assets/weather-icons/line/cloudy.svg" alt="" width="60">
                   <img v-else-if="icon == 'fog'" src="@/assets/weather-icons/line/fog.svg" alt="" width="60">
                   <img v-else-if="icon == 'partly-cloudy-day'" src="@/assets/weather-icons/line/partly-cloudy-day.svg" alt="" width="60">
                   <img v-else-if="icon == 'partly-cloudy-night'" src="@/assets/weather-icons/line/partly-cloudy-night.svg" alt="" width="60">
                   <img v-else-if="icon == 'rain'" src="@/assets/weather-icons/line/rain.svg" alt="" width="60">
                   <img v-else-if="icon == 'rain-snow'" src="@/assets/weather-icons/line/snowflake.svg" alt="" width="60">
                   <img v-else-if="icon == 'rain-snow-showers-day'" src="@/assets/weather-icons/line/snowflake.svg" alt="" width="60">
                   <img v-else-if="icon == 'rain-snow-showers-night'" src="@/assets/weather-icons/line/snowflake.svg" alt="" width="60">
                   <img v-else-if="icon == 'showers-day'" src="@/assets/weather-icons/line/partly-cloudy-day-rain.svg" alt="" width="60">
                   <img v-else-if="icon == 'showers-night'" src="@/assets/weather-icons/line/partly-cloudy-night-rain.svg" alt="" width="60">
                   <img v-else-if="icon == 'sleet'" src="@/assets/weather-icons/line/sleet.svg" alt="" width="60">
                   <img v-else-if="icon == 'snow'" src="@/assets/weather-icons/line/snow.svg" alt="" width="60">
                   <img v-else-if="icon == 'snow-showers-day'" src="@/assets/weather-icons/line/partly-cloudy-day-snow.svg" alt="" width="60">
                   <img v-else-if="icon == 'snow-showers-night'" src="@/assets/weather-icons/line/partly-cloudy-night-snow.svg" alt="" width="60">
                   <img v-else-if="icon == 'thunder'" src="@/assets/weather-icons/line/thunderstorms.svg" alt="" width="60">
                   <img v-else-if="icon == 'thunder-rain'" src="@/assets/weather-icons/line/thunderstorms-rain.svg" alt="" width="60">
                   <img v-else-if="icon == 'thunder-showers-day'" src="@/assets/weather-icons/line/thunderstorms-day-rain.svg" alt="" width="60">
                   <img v-else-if="icon == 'thunder-showers-night'" src="@/assets/weather-icons/line/thunderstorms-night-rain.svg" alt="" width="60">
                   <img v-else-if="icon == 'wind'" src="@/assets/weather-icons/line/wind.svg" alt="" width="60">
                   <img v-else src="@/assets/weather-icons/line/not-available.svg" alt="" width="60">
                   {{temp}}&deg;
                </div>
                <div class="desc">
                    {{desc}}
                </div>
            </div>
            <div class="right-panel panel">
                <div class="desc">
                    Visibility 
                    <div class="desc-item">
                     {{visibility}} {{this.$store.state.unitDistance}}
                    </div>
                </div><hr/>
                <div class="desc">
                    Pressure   
                    <div class="desc-item">
                     {{pressure}} mb
                    </div>
                </div><hr/>
                <div class="desc">
                    Wind Speed
                    <div class="desc-item">
                     {{windspeed}} {{this.$store.state.unitDistance}}/h
                    </div>
                </div><hr/>
                <div class="desc">
                    Wind Gust
                    <div class="desc-item">
                     {{windgust}} {{this.$store.state.unitDistance}}/h
                    </div>
                </div>
                <router-link to="/forecast/today" class="link"><p class="link" v-on:click="saveLocation">MORE DETAILS <i class="fas fa-search"></i></p></router-link>
                
            </div>
            </div>
            
        </div>
</template>

<script>
export default {
    name:"currentWeatherCard",
    props:["date", "time", "address", "temp", "icon", "desc", "visibility", "pressure", "windspeed", "windgust"],
    methods:{
        saveLocation(){
            if(!this.address.includes("/")){
                this.$store.commit('search', this.address)
            }else{
                return
            }
        },
        updateIcon(){
            if(this.icon == "clear-day"){
                return "@/assets/weather-icons/line/clear-day.svg"
            }else if(this.icon == "rain"){
                return "@/assets/weather-icons/line/rain.svg"
            }
        }
    }
}
</script>


<style scoped>

div.widget1{
    position: relative;
    background-color: #fcfdfd;
    border-radius: 9px;
    display: flex;
    padding: 23px;
    max-width: 600px;
    box-shadow: 0px 31px 45px -26px #080c21;
}

div.left-panel
{
    text-align: left;
}

div.date
{
  font-size: 14px;
  font-weight: bold;
  color: rgba(0,0,0,0.5);
}

div.city
{
  font-size: 21px;
  font-weight: bold;
  text-transform: uppercase;
  padding-top: 5px;
  color: rgba(0,0,0,0.7);
}

div.temp
{
  font-size: 81px;
  color: rgba(0,0,0,0.9);
  font-weight: 100;
}

.desc{
    font-family: 'Lato', sans-serif;
    text-align: left;
    color: rgba(0,0,0,0.9);
    display: flex;
    justify-content: space-between;
}

div.panel
{
  display: inline-block;
}

div.right-panel
{
    width: 280px;
    top: 0;
    margin-top: 25px;
    right: 30px;
}

.link{
    padding-top: 20px;
    color:#41993b;
    text-decoration: none;
}

.link:hover{
    color: #2f722a;
}
/*min-width:540px*/
@media only screen and (min-width:1200px){
    .right-panel{
        position: absolute;
    }

}

</style>