<template>
    <div class="main-bg">
        <nav>
            <ul>
                <li><img class="logo" src="@/assets/hubble.png" alt=""></li>
            </ul>
            <ul>
                <li><i class="fas fa-ellipsis-v" v-on:click="openSettings"></i></li>
                <!--<li><router-link to="/about">About</router-link></li>-->
            </ul>
        </nav>
        <main class="search">
            <div class="search-icon">
                <i class="fas fa-search"></i>
            </div>
            <input v-model="search" v-on:keyup.enter="updateLocation(this.search)" class="search-main" type="text" placeholder="Search location or longitude, latitude">
        </main>
        <Settings v-if="settings" @close-settings="closeSettings" @update-data="updateData"/>
        <main>
            <!--TODO: recent locations
            <h4 class="recent-header">Recent Locations</h4>
            <div class="div-center">
                <recentWeatherCard/>
                <recentWeatherCard/>
                <recentWeatherCard/>
            </div>-->

            <div class="div-center2">
                <h1 aria-busy="true" v-if="loading"></h1>
                <currentWeatherCard v-else :date="this.currentDate" :time="this.currentTime" :icon="this.currentIcon" :address="this.currentAddress" :temp="this.currentTemp" :visibility="this.currentVisibility" :pressure="this.currentPressure" :windspeed="this.currentWindSpeed" :windgust="this.currentWindGust" :desc="this.currentDesc"/>
            </div>

        </main>
        </div>
    
</template>


<script>
import currentWeatherCard from "@/components/currentWeatherCard.vue"
import recentWeatherCard from "@/components/recentWeatherCard.vue"
import Settings from "@/components/settings.vue"
import hubble from "../hubble"
import { useGeolocation } from '@vueuse/core'
const { coords, locatedAt, error } = useGeolocation()



export default {
    name:"mainIndex",
    data(){
        return{
            settings:this.$store.state.hubbleSettings,
            current:[],
            currentDate:"",
            currentTime:"",
            currentAddress:"",
            currentDesc:"",
            currentTemp:"",
            currentIcon:"",
            currentVisibility:"",
            currentPressure:"",
            currentWindSpeed:"",
            currentWindGust:"",
            search:"",
            longitude:0,
            latitude:0,
            position:"",
            loading:true
        }
    },
    components:{
        currentWeatherCard,
        recentWeatherCard,
        Settings
    },
    methods:{
        openSettings(){
            this.$store.commit('hubbleSettingsOpen')
            this.settings = this.$store.state.hubbleSettings
            //localStorage.setItem("hubble_settings", true)
            //this.settings = localStorage.getItem("hubble_settings")
        },
        closeSettings(){
            this.$store.commit('hubbleSettingsClose')
            this.settings = this.$store.state.hubbleSettings
            //localStorage.setItem("hubble_settings", false)
            //this.settings = false
        },
        async updateData(){
            this.loading = true
            if(this.search){
                this.loading = true
                this.current = await hubble.getCurrentWeather(this.search,this.$store.state.unit)
                this.$store.commit('resolveAddress', this.current[0].resolvedAddress)
                this.currentDate = this.current[0].days[0].datetime
                this.currentTime = this.current[0].currentConditions.datetime
                this.currentAddress = this.current[0].address
                this.currentDesc = this.current[0].currentConditions.conditions
                this.currentTemp = this.current[0].currentConditions.temp
                this.currentIcon = this.current[0].currentConditions.icon
                this.currentVisibility = this.current[0].currentConditions.visibility
                this.currentPressure = this.current[0].currentConditions.pressure
                this.currentWindSpeed = this.current[0].currentConditions.windspeed
                this.currentWindGust = this.current[0].currentConditions.windgust
                this.loading = false
            }else{
                const location = await hubble.getLocation()
                this.longitude = location.geoplugin_longitude
                this.latitude = location.geoplugin_latitude
                this.position = location.geoplugin_timezone
                this.current = await hubble.getCurrentWeather(`${this.longitude},${this.latitude}`,this.$store.state.unit)
                this.$store.commit('resolveAddress', this.current[0].resolvedAddress)
                this.currentDate = this.current[0].days[0].datetime
                this.currentTime = this.current[0].currentConditions.datetime
                this.currentAddress = this.position
                this.currentDesc = this.current[0].currentConditions.conditions
                this.currentTemp = this.current[0].currentConditions.temp
                this.currentIcon = this.current[0].currentConditions.icon
                this.currentVisibility = this.current[0].currentConditions.visibility
                this.currentPressure = this.current[0].currentConditions.pressure
                this.currentWindSpeed = this.current[0].currentConditions.windspeed
                this.currentWindGust = this.current[0].currentConditions.windgust
                this.loading = false
            }
        },
        async updateLocation(loc){
            this.loading = true
            this.current = await hubble.getCurrentWeather(loc,this.$store.state.unit)
            this.$store.commit('resolveAddress', this.current[0].resolvedAddress)
            this.currentDate = this.current[0].days[0].datetime
            this.currentTime = this.current[0].currentConditions.datetime
            this.currentAddress = this.current[0].address
            this.currentDesc = this.current[0].currentConditions.conditions
            this.currentTemp = this.current[0].currentConditions.temp
            this.currentIcon = this.current[0].currentConditions.icon
            this.currentVisibility = this.current[0].currentConditions.visibility
            this.currentPressure = this.current[0].currentConditions.pressure
            this.currentWindSpeed = this.current[0].currentConditions.windspeed
            this.currentWindGust = this.current[0].currentConditions.windgust
            this.loading = false
            this.$store.commit('search', this.search)
        }
    },
    async created(){
        const location = await hubble.getLocation()
        this.longitude = location.geoplugin_longitude
        this.latitude = location.geoplugin_latitude
        this.position = location.geoplugin_timezone
        //console.log(location.geoplugin_longitude)
        //console.log(location.geoplugin_latitude)
        this.current = await hubble.getCurrentWeather(`${this.longitude},${this.latitude}`,this.$store.state.unit)
        this.$store.commit('resolveAddress', this.current[0].resolvedAddress)
        this.currentDate = this.current[0].days[0].datetime
        this.currentTime = this.current[0].currentConditions.datetime
        //this.currentAddress = this.current[0].address
        this.currentAddress = this.position
        this.currentDesc = this.current[0].currentConditions.conditions
        this.currentTemp = this.current[0].currentConditions.temp
        this.currentIcon = this.current[0].currentConditions.icon
        this.currentVisibility = this.current[0].currentConditions.visibility
        this.currentPressure = this.current[0].currentConditions.pressure
        this.currentWindSpeed = this.current[0].currentConditions.windspeed
        this.currentWindGust = this.current[0].currentConditions.windgust
        this.loading = false
        //console.log(this.current[0].days[0].datetime)
        //console.log(this.currentTemp)
    }
}
</script>

<style scoped>
.logo{
    width: 230px;
    height: 50px;
}

.main-bg{
    background-image: url("@/assets/weather2.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    height: 100%;
    padding-bottom: 350px;
}

nav{
    padding-top: 0;
}

.search{
    width: 450px;
    display: flex;
    margin: auto;
}

.search-main{
    background-color: #fff;
    border: none;
    border-radius: 0;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
}

.fas{
    margin-top: 20px;
}
.search-icon{
    background-color: #fff;
    margin: 0;
    height: 62px;
    padding-left: 20px;
    padding-right: 10px;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
}
.div-center{
    display: flex;
    justify-content: center;
}

.div-center2{
    margin-left: 27%;
    margin-right: 27%;
}

.recent-header{
    margin-bottom: 10px;
}
@media only screen and (max-width:1183px){
    .search-icon{
        height: 59px;
    }
    @media only screen and (max-width:1169px){
        .search-icon{
            height: 59px;
        }
    }
    @media only screen and (max-width:986px){
        .search-icon{
            height: 56px;
        }
    }
    @media only screen and (max-width:766px){
        .search-icon{
            height: 53px;
        }
    }
    @media only screen and (max-width:573px){
        .search-icon{
            height: 50px;
        }
        .fas{
            margin-top:15px;
        }

        .search{
            width: 350px;
        }
    }
    @media only screen and (max-width:350px){
        .search{
            width: 300px;
        }
    }

}

@media only screen and (max-width:757px){
    .div-center{
        flex-direction: column;
        margin: auto;
        display: block;
        margin-left: 35%;
        margin-right: 35%;
    }

    @media only screen and (max-width:587px){
        .div-center{
            margin-left: 30%;
            margin-right: 30%;
        }
        .div-center2{
            margin-left: 5%;
            margin-right: 5%;
        }
    }
    @media only screen and (max-width:500px){
        .div-center{
            margin-left: 25%;
            margin-right: 25%;
        }
    }
    @media only screen and (max-width:400px){
        .div-center{
            margin-left: 20%;
            margin-right: 20%;
        }
        .div-center2{
            margin-left: 0;
            margin-right: 0;
        }


    }
}

.fa-ellipsis-v{
    cursor: pointer;
}


</style>