import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/forecast/today',
    name: 'forecast_today',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/ForecastView.vue')
  },
  {
    path: '/forecast/tomorrow',
    name: 'forecast_tomorrow',
    component: () => import('../views/NextForecastView.vue')
  },
  {
    path: '/forecast/hourly',
    name: 'forecast_hourly',
    component: () => import('../views/HourlyForecastView.vue')
  },
  {
    path: '/forecast/next7',
    name: 'forecast_next7',
    component: () => import('../views/Next7ForecastView.vue')
  },
  {
    path: '/forecast/past7',
    name: 'forecast_past7',
    component: () => import('../views/Past7ForecastView.vue')
  },
  {
    path: '/forecast/next30',
    name: 'forecast_next30',
    component: () => import('../views/Next30ForecastView.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
